import React, { Component } from 'react';
import PieChart from 'react-minimal-pie-chart';


import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';


class CustomDashboard extends Component {
	render() {
		const { piechartData } = this.props;
		const { callCount } = this.props;

		return (
			<header
				className="App-header"
				onClick={ () => this.props.toggle_sidebar() }
			>
				<List>
					<ListItem>
						<Grid container justify="center">
							<Typography
								style={{
									fontSize: '30px',
									color: "white"
								}}
							>
								Possible Failure Detection
							</Typography>
						</Grid>
					</ListItem>
					<ListItem>
						<Grid container justify="center">
							<p>Arrived calls: { callCount }</p>
						</Grid>
					</ListItem>
					<ListItem>
						<Grid container justify="center">
							<PieChart
								style={{ height: "300px" }}
								onClick={
									(event, data, dataIndex) =>
									this.props.piechart_tapped(event, data, dataIndex, "piechartData")
								}
								data={ piechartData }
								label
								labelStyle={{
									fontSize: '5px',
									fontFamily: 'sans-serif',
									fill: '#121212'
								}}
							/>
						</Grid>
					</ListItem>
					{ piechartData.map((cluster, i) =>
						<ListItem key={ i }>
							<Grid container justify="center">
								<Typography
									style={{
										fontSize: '20px',
										color: "white"
									}}
								>
									{ cluster["value"] } queries related to "{ cluster["description"] }"
								</Typography>
							</Grid>
						</ListItem>
					)}
				</List>
			</header>
		)
	}
}

export default CustomDashboard;
