import React, { Component } from 'react';
import './App.css';
import axios from 'axios';


import CustomDashboard from "./CustomDashboard";
import CustomModal from "./CustomModal";
import CustomSidebar from "./CustomSidebar";


class App extends Component {
	constructor(props) {
		super(props);
		this.state = {
			piechartData: [],
			callCount: 0,
			modalData: {},

			isSidebarOpen: false,
			isModalOpen: false,
			isProgressVisible: false,

			whichIndex: 0,
			messageData: {}
		}
	}

	getRandomColor() {
		const letters = "6789ABCDEF";
		var color = '#';
		for (var i = 0; i < 6; i++) {
			color += letters[Math.floor(Math.random() * 10)];
		}
		return color;
	}

	reset_message = () => {
		this.setState({
			messageData: {}
		});
	}

	componentDidMount() {
		this.get_current_stats();
	}

	close_modal = () => {
		this.setState({ isModalOpen: false });
	}

	toggle_sidebar = () => {
		// this.setState({ isSidebarOpen: !this.state.isSidebarOpen });
	}

	piechart_tapped = (event, data, dataIndex, whichChart) => {
		event.stopPropagation();
		let modalData = {};

		if (whichChart === "piechartData") {
			modalData = this.state.piechartData[dataIndex];
		}
		this.setState({
			isModalOpen: true,
			modalData: modalData
		});
	}

	get_current_stats() {
		axios.get(
			"http://10.77.63.156:2040/get_current_stats",
		).then(res => {
			const clusterData = res.data.data;
			let similarityPieData = [];

			// process cluster data
			clusterData["clusters"].forEach((cluster, index) => {
				similarityPieData.push({
					"title": "Cluster " + index,
					"description": cluster["description"],
					"value": cluster["count"],
					"color": this.getRandomColor()
				});
			});

			this.setState({
				piechartData: similarityPieData,
				callCount: clusterData["number_of_calls"]
			});
		}).catch(err => {
			console.log(err);
		});
	}

	upload_audio_file = (fileName, file) => {
		if (fileName === "" || !file) {
			this.setState({
				messageData: {
					"status": "KO",
					"message": "file name required"
				}
			});
		} else {
			this.setState({
				isProgressVisible: true,
				messageData: {}
			});
			const formData = new FormData();
			formData.append(fileName, file)

			axios.post(
				"http://localhost:5000/upload_audio_file",
				formData
			).then(res => {
				this.setState({
					isProgressVisible: false,
					messageData: res.data
				});
			}).catch(err => {
				console.log(err);
			});
		}
	}

	convert_speech_to_text = (fileName) => {
		if (fileName === "") {
			this.setState({
				messageData: {
					"status": "KO",
					"message": "file name required"
				}
			});
		} else {
			this.setState({
				isProgressVisible: true,
				messageData: {}
			});
			axios.post(
				"http://localhost:5000/convert_speech_to_text",
				{ file_name: fileName }
			).then(res => {
				this.setState({
					isProgressVisible: false,
					messageData: res.data
				});
			}).catch(err => {
				console.log(err);
			});
		}
	}

	find_entities = (fileName) => {
		if (fileName === "") {
			this.setState({
				messageData: {
					"status": "KO",
					"message": "file name required"
				}
			});
		} else {
			this.setState({
				isProgressVisible: true,
				messageData: {}
			});
			axios.post(
				"http://localhost:5000/find_entities",
				{ file_name: fileName }
			).then(res => {
				this.setState({
					isProgressVisible: false,
					messageData: res.data
				});
			}).catch(err => {
				console.log(err);
			});
		}
	}

	cluster_sentence = (fileName) => {
		if (fileName === "") {
			this.setState({
				messageData: {
					"status": "KO",
					"message": "file name required"
				}
			});
		} else {
			this.setState({
				isProgressVisible: true,
				messageData: {}
			});
			axios.post(
				"http://localhost:5000/cluster_sentence",
				{ file_name: fileName }
			).then(res => {
				this.setState({
					isProgressVisible: false,
					messageData: res.data
				});

				if (res.data.status === "OK") {
					this.get_current_stats();
				}
			}).catch(err => {
				console.log(err);
			});
		}
	}

	render() {
		return (
			<div className="App">
				<CustomDashboard
					piechartData={ this.state.piechartData }
					callCount={ this.state.callCount }
					piechart_tapped={ this.piechart_tapped }
					toggle_sidebar={ this.toggle_sidebar }
				/>
				<CustomModal
					open={ this.state.isModalOpen }
					data={ this.state.modalData }
					close_modal={ this.close_modal }
				/>
				<CustomSidebar
					open={ this.state.isSidebarOpen }
					toggle_sidebar={ this.toggle_sidebar }
					upload_audio_file={ this.upload_audio_file }
					convert_speech_to_text={ this.convert_speech_to_text }
					find_entities={ this.find_entities }
					cluster_sentence={ this.cluster_sentence }
					isProgressVisible={ this.state.isProgressVisible }
					messageData={ this.state.messageData }
					reset_message={ this.reset_message }
				/>
			</div>
		);
	}
}

export default App;
