import React, { Component } from 'react';
import { ReactMic } from 'react-mic';


import Drawer from '@material-ui/core/Drawer';
import ExpansionPanel from '@material-ui/core/ExpansionPanel';
import ExpansionPanelSummary from '@material-ui/core/ExpansionPanelSummary';
import ExpansionPanelDetails from '@material-ui/core/ExpansionPanelDetails';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import CircularProgress from '@material-ui/core/CircularProgress';
import Input from '@material-ui/core/Input';
import { withStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';


const styles = theme => (
	{
		root: {
		flexGrow: 1,
	},
	paper: {
	  padding: theme.spacing.unit * 2,
	  textAlign: 'center',
	  color: theme.palette.text.secondary,
	},
});

class CustomSidebar extends Component {
	constructor(props) {
		super(props);
		this.state = {
			expanded: false,
			textFieldData: "",
			record: false,
			blobUrl: "",
			uploadedFile: null
		}
	}

	start_recording = () => {
	this.setState({
		record: true
	});
	}

	stop_recording = () => {
	this.setState({
		record: false
	});
	}

	on_data = (recordedBlob) => {
		// console.log('chunk of real-time data is: ', recordedBlob);
	}

	on_stop = (recordedBlob) => {
		// console.log('recordedBlob is: ', recordedBlob);
		this.setState({
			blobUrl: recordedBlob.blobURL
		});
	}

	handle_expansion_panel = (panel) => (event, expanded) => {
		this.setState({
			expanded: expanded ? panel : false
		});
		this.props.reset_message();
	};

	handle_text_change = (event) => {
		this.setState({
			textFieldData: event.target.value
		});
	}

	handle_upload = (event) => {
		console.log(event);
		this.setState({
			uploadedFile: event.target.files[0]
		});
	}

	render() {
		const { open, isProgressVisible, messageData } = this.props;

		return (
			<Drawer
				open={ open }
				onClose={ () => this.props.toggle_sidebar() }
			>
				<Typography
					gutterBottom variant="h5" component="h2"
					style={{ margin: 20 }}
				>
					Analyze new audio file
				</Typography>
				<ExpansionPanel
					square
					expanded={ this.state.expanded === "panel1" }
					onChange={ this.handle_expansion_panel("panel1") }
				>
					<ExpansionPanelSummary expandIcon={ <ExpandMoreIcon /> }>
						<Typography color="primary" variant="subtitle1">Upload recorded file</Typography>
					</ExpansionPanelSummary>
					<ExpansionPanelDetails>
						<List>
							<ListItem>
								<Typography><b>Record audio from mic</b></Typography>
							</ListItem>
							<ListItem>
								<ReactMic
									record={ this.state.record }
									className="sound-wave"
									onStop={ this.on_stop }
									onData={ this.on_data }
									strokeColor="#000000"
									backgroundColor="#52BE80"
								/>
							</ListItem>
							<ListItem>
								<Grid container spacing={8}>
									<Grid item xs={4}>
										<Button
											variant="contained"
											color="primary"
											onClick={
												() => this.start_recording()
											}
										>
											Start recording
										</Button>
									</Grid>
									<Grid item xs={4}>
										<Button
											variant="contained"
											color="primary"
											onClick={
												() => this.stop_recording()
											}
											style={{
												alignItems: 'flex-end'
											}}
										>
											Stop recording
										</Button>
									</Grid>
									<Grid item xs={4}>
										<a
											href={ this.state.blobUrl }
											download="Untitled.wav"
											style={{ textDecoration: "none" }}
										>
											<Button
												variant="contained"
												color="primary"
											>
												Download recording
											</Button>
										</a>
									</Grid>
								</Grid>
							</ListItem>
							<ListItem>
								{ isProgressVisible ? <CircularProgress /> : null }
								<Typography
									color={
										messageData && messageData.status === "OK" ? "default" : "error"
									}
								>
									{	messageData ? messageData.message : null }
								</Typography>
							</ListItem>
							<ListItem>
								<Typography><b>* Enter .wav filename without extension</b></Typography>
							</ListItem>
							<ListItem>
								<TextField
									id="outlined-name"
									label="Audio Filename"
									value={ this.state.textFieldData }
									onChange={ (event) => this.handle_text_change(event) }
									margin="normal"
									variant="outlined"
									required={ true }
								/>
							</ListItem>
							<ListItem>
								<Grid container spacing={40}>
									<Grid item xs={6}>
										<Input
											accept="audio/wav"
											type="file"
											onChange={ (event) => this.handle_upload(event) }
											placeholder="abc"
										/>
									</Grid>
									<Grid item xs={6}>
										<Button
											variant="contained"
											color="primary"
											onClick={
												() => this.props.upload_audio_file(
													this.state.textFieldData,
													this.state.uploadedFile
												)
											}
										>
											Upload
										</Button>
									</Grid>
								</Grid>
							</ListItem>
						</List>
					</ExpansionPanelDetails>
				</ExpansionPanel>
				<ExpansionPanel
					square
					expanded={ this.state.expanded === "panel2" }
					onChange={ this.handle_expansion_panel("panel2") }
				>
					<ExpansionPanelSummary expandIcon={ <ExpandMoreIcon /> }>
						<Typography color="primary" variant="subtitle1">Speech-to-Text</Typography>
					</ExpansionPanelSummary>
					<ExpansionPanelDetails>
						<List>
							<ListItem>
								<Typography><b>* Enter .wav filename without extension</b></Typography>
							</ListItem>
							<ListItem>
								{ isProgressVisible ? <CircularProgress /> : null }
								<Typography
									width={ 300 }
									color={
										messageData && messageData.status === "OK" ? "default" : "error"
									}
								>
									{	messageData ? messageData.message : null }
								</Typography>
							</ListItem>
							<ListItem>
								<TextField
									id="outlined-name"
									label="WAV Filename"
									value={ this.state.textFieldData }
									onChange={ (event) => this.handle_text_change(event) }
									margin="normal"
									variant="outlined"
									required={ true }
								/>
							</ListItem>
							<ListItem>
								<Button
									variant="contained"
									color="primary"
									onClick={
										() => this.props.convert_speech_to_text(this.state.textFieldData)
									}
								>
									Send
								</Button>
							</ListItem>
						</List>
					</ExpansionPanelDetails>
				</ExpansionPanel>
				<ExpansionPanel
					square
					expanded={ this.state.expanded === "panel3" }
					onChange={ this.handle_expansion_panel("panel3") }
				>
					<ExpansionPanelSummary expandIcon={ <ExpandMoreIcon /> }>
						<Typography color="primary" variant="subtitle1">Entity Extraction</Typography>
					</ExpansionPanelSummary>
					<ExpansionPanelDetails>
						<List>
							<ListItem>
								<Typography><b>* Enter unprocessed .txt filename without extension</b></Typography>
							</ListItem>
							<ListItem>
								{ isProgressVisible ? <CircularProgress /> : null }
								<Typography
									color={
										messageData && messageData.status === "OK" ? "default" : "error"
									}
								>
									{	messageData ? messageData.message : null }
								</Typography>
							</ListItem>
							<ListItem>
								<TextField
									id="outlined-name"
									label="Text Filename"
									value={ this.state.textFieldData }
									onChange={ (event) => this.handle_text_change(event) }
									margin="normal"
									variant="outlined"
									required={ true }
								/>
							</ListItem>
							<ListItem>
								<Button
									variant="contained"
									color="primary"
									onClick={
										() => this.props.find_entities(this.state.textFieldData)
									}
								>
									Send
								</Button>
							</ListItem>
						</List>
					</ExpansionPanelDetails>
				</ExpansionPanel>
				<ExpansionPanel
					square
					expanded={ this.state.expanded === "panel4" }
					onChange={ this.handle_expansion_panel("panel4") }
				>
					<ExpansionPanelSummary expandIcon={ <ExpandMoreIcon /> }>
						<Typography color="primary" variant="subtitle1">Sentence Clustering</Typography>
					</ExpansionPanelSummary>
					<ExpansionPanelDetails>
						<List>
							<ListItem>
								<Typography><b>* Enter .txt filename without extension</b></Typography>
							</ListItem>
							<ListItem>
								{ isProgressVisible ? <CircularProgress /> : null }
								<Typography
									color={
										messageData && messageData.status === "OK" ? "default" : "error"
									}
								>
									{	messageData ? messageData.message : null }
								</Typography>
							</ListItem>
							<ListItem>
								<TextField
									id="outlined-name"
									label="Text Filename"
									value={ this.state.textFieldData }
									onChange={ (event) => this.handle_text_change(event) }
									margin="normal"
									variant="outlined"
									required={ true }
								/>
							</ListItem>
							<ListItem>
								<Button
									variant="contained"
									color="primary"
									onClick={
										() => this.props.cluster_sentence(this.state.textFieldData)
									}
								>
									Send
								</Button>
							</ListItem>
						</List>
					</ExpansionPanelDetails>
				</ExpansionPanel>
			</Drawer>
		);
	}
}

export default withStyles(styles)(CustomSidebar);
